"use client"

import { useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { exercises, categories, getExercisesByCategory } from '@/lib/exercises'
import type { Exercise } from '@/lib/exercises'

export default function ExercisesPage() {
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedDifficulty, setSelectedDifficulty] = useState<string>('all')

  const filterExercises = (categoryExercises: Exercise[]) => {
    return categoryExercises.filter(exercise => {
      const matchesSearch = exercise.title.fr.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           exercise.description.fr.toLowerCase().includes(searchTerm.toLowerCase())
      const matchesDifficulty = selectedDifficulty === 'all' || exercise.difficulty === selectedDifficulty
      return matchesSearch && matchesDifficulty
    })
  }

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'facile': return 'bg-green-500'
      case 'moyen': return 'bg-yellow-500'
      case 'difficile': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'avec-ballon': return '⚽'
      case 'sans-ballon': return '🏃'
      case 'physique': return '💪'
      case 'cardio': return '❤️'
      case 'technique-tactique': return '🧠'
      default: return '📋'
    }
  }

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'avec-ballon': return 'from-blue-900/40 to-blue-800/40 border-blue-500/30'
      case 'sans-ballon': return 'from-green-900/40 to-green-800/40 border-green-500/30'
      case 'physique': return 'from-red-900/40 to-red-800/40 border-red-500/30'
      case 'cardio': return 'from-pink-900/40 to-pink-800/40 border-pink-500/30'
      case 'technique-tactique': return 'from-purple-900/40 to-purple-800/40 border-purple-500/30'
      default: return 'from-gray-900/40 to-gray-800/40 border-gray-500/30'
    }
  }

  return (
    <div className="min-h-screen p-4">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Button 
            variant="ghost" 
            className="text-purple-300 hover:text-white mb-4"
            onClick={() => window.history.back()}
          >
            ← Retour
          </Button>
          <h1 className="text-3xl font-bold text-white mb-4">Base d'Exercices</h1>
          
          {/* Search and Filters */}
          <div className="flex flex-col sm:flex-row gap-4 mb-6">
            <div className="flex-1">
              <Input
                placeholder="Rechercher un exercice..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="bg-purple-900/20 border-purple-500/30 text-white placeholder:text-purple-300"
              />
            </div>
            <div className="flex gap-2">
              {['all', 'facile', 'moyen', 'difficile'].map((difficulty) => (
                <Button
                  key={difficulty}
                  variant={selectedDifficulty === difficulty ? "default" : "outline"}
                  size="sm"
                  className={`${
                    selectedDifficulty === difficulty 
                      ? 'bg-purple-600 hover:bg-purple-700' 
                      : 'border-purple-500/50 text-purple-300 hover:bg-purple-900/30'
                  }`}
                  onClick={() => setSelectedDifficulty(difficulty)}
                >
                  {difficulty === 'all' ? 'Tous' : difficulty}
                </Button>
              ))}
            </div>
          </div>
        </div>

        {/* Exercise Categories */}
        <Tabs defaultValue="avec-ballon" className="w-full">
          <TabsList className="grid w-full grid-cols-5 bg-purple-900/30 border-purple-500/30">
            {Object.entries(categories).map(([key, category]) => (
              <TabsTrigger 
                key={key} 
                value={key}
                className="data-[state=active]:bg-purple-600 data-[state=active]:text-white text-purple-300"
              >
                <span className="mr-1 hidden sm:inline">{getCategoryIcon(key)}</span>
                <span className="hidden sm:inline">{category.fr}</span>
                <span className="sm:hidden">{getCategoryIcon(key)}</span>
              </TabsTrigger>
            ))}
          </TabsList>

          {Object.entries(categories).map(([categoryKey, category]) => {
            const categoryExercises = getExercisesByCategory(categoryKey)
            const filteredExercises = filterExercises(categoryExercises)

            return (
              <TabsContent key={categoryKey} value={categoryKey} className="mt-6">
                <div className="mb-4">
                  <h2 className="text-2xl font-semibold text-white mb-2 flex items-center gap-2">
                    <span>{getCategoryIcon(categoryKey)}</span>
                    {category.fr}
                  </h2>
                  <p className="text-purple-300">
                    {filteredExercises.length} exercice{filteredExercises.length > 1 ? 's' : ''} disponible{filteredExercises.length > 1 ? 's' : ''}
                  </p>
                </div>

                {filteredExercises.length === 0 ? (
                  <Card className="bg-purple-900/20 border-purple-500/30">
                    <CardContent className="p-8 text-center">
                      <p className="text-purple-300">Aucun exercice trouvé avec ces critères.</p>
                    </CardContent>
                  </Card>
                ) : (
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {filteredExercises.map((exercise) => (
                      <Card 
                        key={exercise.id} 
                        className={`bg-gradient-to-br ${getCategoryColor(categoryKey)} hover:scale-105 transition-all duration-300`}
                      >
                        <CardHeader className="pb-3">
                          <div className="flex justify-between items-start mb-2">
                            <CardTitle className="text-white text-lg leading-tight">
                              {exercise.title.fr}
                            </CardTitle>
                            <div className="flex flex-col gap-1 ml-2">
                              <Badge 
                                variant="outline" 
                                className="border-white/30 text-white text-xs"
                              >
                                {exercise.duration}
                              </Badge>
                              <Badge 
                                className={`${getDifficultyColor(exercise.difficulty)} text-white text-xs`}
                              >
                                {exercise.difficulty}
                              </Badge>
                            </div>
                          </div>
                        </CardHeader>
                        <CardContent className="pt-0">
                          <p className="text-white/80 text-sm mb-3 leading-relaxed">
                            {exercise.description.fr}
                          </p>
                          <div className="flex justify-between items-center">
                            <Badge 
                              variant="secondary" 
                              className="bg-white/10 text-white/90 text-xs"
                            >
                              {exercise.category.replace('-', ' ')}
                            </Badge>
                            <Button 
                              size="sm" 
                              variant="ghost"
                              className="text-white/70 hover:text-white hover:bg-white/10 text-xs"
                            >
                              Détails →
                            </Button>
                          </div>
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                )}
              </TabsContent>
            )
          })}
        </Tabs>

        {/* Statistics */}
        <Card className="mt-8 bg-gradient-to-r from-purple-900/30 to-pink-900/30 border-purple-500/30">
          <CardHeader>
            <CardTitle className="text-white flex items-center gap-2">
              <span>📊</span> Statistiques
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
              {Object.entries(categories).map(([key, category]) => {
                const count = getExercisesByCategory(key).length
                return (
                  <div key={key} className="text-center">
                    <div className="text-2xl font-bold text-white">{count}</div>
                    <div className="text-purple-300 text-sm">{category.fr}</div>
                  </div>
                )
              })}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
